}

.btnshop {
  text-decoration: none;
  border: 3px solid white;
  background: none;

  padding: 4px;

  color: white;
  font-family: "Raleway";
  font-size: 21px;
  cursor: pointer;
  transition: 0.8s;

  overflow: hidden;
  position: absolute;

  display: flex;
  justify-content: space-around;
  align-items: center;
}
.btnshop:hover {
  color: black;
  z-index: 1;
  border: 3px solid black;
}
.btnshop::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: white;
  z-index: -1;
  transition: 0.8s;
  top: 0;
  border-radius: 0 0 50% 50%;
}
.btnshop:hover::after {
  height: 180%;
}
